// Actions / UserAction

// Modules
import axios from "axios";

// Config
import config from "../config";


export function getAllMessages() {
    return dispatch => {
        return axios.get(config.serverURL + "/message", {
            withCredentials: true
        }).then((res) => {

            dispatch({
                type: "MESSAGE_SET_GROUP",
                payload: res.data
            });

            return true;
        }).catch((err) => {
            console.log(err);
        })
    }
}


export function getMessageChat(arg) {
    return dispatch => {
        return axios.put(config.serverURL + "/message/chat", {
            message_uuid: arg.msgUUID
        }, {
            withCredentials: true
        }).then((res) => {
            dispatch({
                type: "MESSAGE_SELECTED",
                payload: res.data
            });

            return true;
        }).catch((err) => {
            console.log(err);
        })
    }
}


export function getRecipient() {
    return dispatch => {
        return axios.get(config.serverURL + "/message/recipient", {
            withCredentials: true
        }).then((res) => {

            dispatch({
                type: "MESSAGE_SET_RECIPIENT",
                payload: res.data
            });

            return true;
        }).catch((err) => {
            console.log(err);
        })
    }
}



export function createNewMessage(arg) {
    return dispatch => {
        return axios.post(config.serverURL + "/message/", {
            subject: arg.subject,
            recipient_uuid: arg.recipientUUID,
            message_type: arg.msgType,
            content: arg.content
        }, {
            withCredentials: true
        }).then((res) => {
            return true;
        }).catch((err) => {
            console.log(err);
        })
    }
}



export function replyMessage(arg) {
    return dispatch => {
        return axios.post(config.serverURL + "/message/chat", {
            message_uuid: arg.msgUUID,
            content: arg.content
        }, {
            withCredentials: true
        }).then((res) => {
            // dispatch({
            //     type: "MESSAGE_SELECTED",
            //     payload: res.data
            // });

            return true;
        }).catch((err) => {
            console.log(err);
        })
    }
}

export function replyMessageSocket(arg) {
    return dispatch => {
        dispatch({
            type: "MESSAGE_UPDATE_CHAT",
            payload: arg
        });
    }
}


export function clearAllMessageState() {
    return dispatch => {
        dispatch({
            type: "MESSAGE_CLEAR"
        });
    }
}


export function clearCurrentMessage() {
    return dispatch => {
        dispatch({
            type: "MESSAGE_CLEAR_CURRENT"
        });
    }
}



