// components / dashboard / conversation / ConversationSidebar

// Modules
import React from "react";
import Link, {navigateTo} from "gatsby-link";
import {connect} from 'react-redux';


export default class ConversationSidebar extends React.Component {
    render() {
        return (
            <div className="col-lg-3">
                <div className="ibox float-e-margins">
                    <div className="ibox-content mailbox-content">
                        <div className="file-manager">
                            <Link className="btn btn-block btn-primary compose-mail" to="/dashboard/conversation/new-message">Start Conversation</Link>
                            <div className="space-25"></div>
                            <h5>Folders</h5>
                            <ul className="folder-list m-b-md" style={{padding: 0}}>
                                <li><Link to="/dashboard/conversation"> <i className="fa fa-inbox "></i> Inbox <span className="label label-warning pull-right">{this.props.messagesLength}</span> </Link></li>
                                <li><a href="#"> <i className="fa fa-envelope-o"></i> Send Mail</a></li>
                                <li><a href="#"> <i className="fa fa-certificate"></i> Important</a></li>
                                <li><a href="#"> <i className="fa fa-file-text-o"></i> Drafts <span className="label label-danger pull-right">2</span></a></li>
                                <li><a href="#"> <i className="fa fa-trash-o"></i> Trash</a></li>
                            </ul>
                            <h5>Categories</h5>
                            <ul className="category-list" style={{padding: 0}}>
                                <li><a href="#"> <i className="fa fa-circle text-primary"></i> Message</a></li>
                                <li><a href="#"> <i className="fa fa-circle text-danger"></i> Complaint</a></li>
                                <li><a href="#"> <i className="fa fa-circle text-info"></i> Suggestion</a></li>
                                <li><a href="#"> <i className="fa fa-circle text-warning"></i> Security</a></li>
                            </ul>

                            <h5 className="tag-title">Labels</h5>
                            <ul className="tag-list" style={{padding: 0}}>
                                <li><a href="#"><i className="fa fa-tag"></i> Message</a></li>
                                <li><a href="#"><i className="fa fa-tag"></i> Complaint</a></li>
                                <li><a href="#"><i className="fa fa-tag"></i> Suggestion</a></li>
                                <li><a href="#"><i className="fa fa-tag"></i> Security</a></li>
                            </ul>
                            <div className="clearfix"></div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
};



