// pages / dashboard / conversation / new-message

// Modules
import React from "react";
import {connect} from 'react-redux';
import Helmet from 'react-helmet';
import {navigateTo} from "gatsby-link";

// Styles
import styles from '../../../style/preloader.module.css';

// Components
import ConversationSidebar from '../../../components/dashboard/conversation/ConversationSidebar';

// Action
import {getRecipient, createNewMessage, clearAllMessageState} from '../../../action/MessageAction';


class ConversationNewMessage extends React.Component {
    componentWillMount() {
        this.props.getRecipient();
    }

    componentDidMount() {
        // console.log(this.props.recipients);
        $(".select-type").select2({
            placeholder: "Select Conversation Type",
            allowClear: true,
            width: '100%'
        });

        $(".select-recipient").select2({
            placeholder: "Select Recipient",
            allowClear: true,
            width: '100%'
        });
    }

    handleSubmit(e) {
        e.preventDefault();

        // clear message in order to show preloader
        this.props.clearAllMessageState();

        // navigate user back to communication page if success
        this.props.createNewMessage({
            subject: this.refs.subject.value,
            recipientUUID: $("#recipient").val(),
            msgType: $("#msgType").val(),
            content: this.refs.message.value
        }).then((res) => {
            if(res) {
                navigateTo('/dashboard/conversation');
            }
        });
    }

    render() {
        if (this.props.user.loggedIn) {

            /* Recipient */
            let recipients = this.props.recipients.map((recipient, index) => {
                // Capitalize recipient type
                let recipientType = recipient.type;
                recipientType = recipientType.replace(recipientType[0], recipientType[0].toUpperCase());

                return (
                    <option key={index} value={recipient.uuid}>
                        {recipientType} - {recipient.name}
                    </option>
                );
            });

            return (
                <div className="wrapper wrapper-content">

                    {/* Start of Helmet */}
                    <Helmet title="Conversation - new" meta={[
                        {
                            name: 'description',
                            content: 'Conversation - new'
                        }, {
                            name: 'keywords',
                            content: 'Conversation'
                        }
                    ]}/>
                    {/* End of Helmet */}


                    {/* Start of Preloader */}
                    <div className={styles.preloader1 + ' ' + (this.props.recipients.length > 0 ? '' : styles.active )}>
                        <i className={styles.preloaderIcon + " fa fa-refresh"}></i>
                    </div>
                    {/* End of Preloader */}


                    {/* Start of Content */}
                    <div className="row">

                        {/* Start of ConversationSidebar */}
                        <ConversationSidebar messagesLength={this.props.messages.length}/>
                        {/* End of ConversationSidebar */}

                        <div className="col-lg-9 animated fadeInRight">
                            <div className="mail-box-header">
                                <div className="pull-right tooltip-demo">
                                    <a href="#" className="btn btn-space btn-white btn-sm" data-toggle="tooltip" data-placement="top" title="Move to draft folder"><i className="fa fa-pencil"></i> Draft</a>
                                    <a href="#" className="btn btn-space btn-danger btn-sm" data-toggle="tooltip" data-placement="top" title="Discard email"><i className="fa fa-times"></i> Discard</a>
                                </div>
                                <h2>
                                        Conversation Content
                                    </h2>
                            </div>
                            <div className="mail-box">


                                <div className="mail-body">

                                    <form className="form-horizontal" onSubmit={this.handleSubmit.bind(this)}>
                                        <div className="form-group">
                                            <label htmlFor="subject" className="col-sm-2 control-label">Subject:</label>
                                            <div className="col-sm-10">
                                                <input type="text" className="form-control" id="subject" ref="subject" maxLength="255" required />
                                            </div>
                                        </div>

                                        <div className="form-group">
                                            <label htmlFor="msgType" className="col-sm-2 control-label">Type:</label>
                                            <div className="col-sm-10">
                                                <select className="select-type form-control" id="msgType" value="" required >
                                                    <option></option>
                                                    <option value="Message">Message</option>
                                                    <option value="Suggestion">Suggestion</option>
                                                    <option value="Complaint">Complaint</option>
                                                    <option value="Security">Security</option>
                                                </select>
                                            </div>
                                        </div>

                                        <div className="form-group">
                                            <label htmlFor="recipient" className="col-sm-2 control-label">Recipient:</label>
                                            <div className="col-sm-10">
                                                <select className="select-recipient form-control" id="recipient" value="" required >
                                                    <option></option>
                                                    {recipients}
                                                </select>
                                            </div>
                                        </div>
                                        <div className="space-25"></div>

                                        <div className="form-group">
                                            <div className="summernote mail-text h-200">
                                                <div className="space-25"></div>
                                                <div className="col-sm-offset-1 col-sm-10">
                                                    <textarea className="form-control" rows="6" placeholder="Please insert your message here" ref="message" maxLength="255" required></textarea>
                                                </div>
                                            </div>
                                        </div>

                                        <div className="form-group">
                                            <div className="mail-body text-right tooltip-demo" style={{paddingBottom: 0}}>
                                                <button type="submit" className="btn btn-space btn-sm btn-primary"><strong><i className="fa fa-reply"></i> Send</strong></button>
                                                <a href="#" className="btn btn-space btn-white btn-sm" data-toggle="tooltip" data-placement="top" title="Discard email"><i className="fa fa-times"></i> Discard</a>
                                                <a href="#" className="btn btn-space btn-white btn-sm" data-toggle="tooltip" data-placement="top" title="Move to draft folder"><i className="fa fa-pencil"></i> Draft</a>
                                            </div>
                                        </div>
                                    </form>
                                </div>
                                <div className="clearfix"></div>



                            </div>
                        </div>
                    </div>
                    {/* End of Content */}


                </div>
            );
        } else {
            return null;
        }
    }
};

const mapStateToProps = (state) => {
    return {user: state.users.user, messages: state.messages.messages, recipients: state.messages.recipients};
}

const mapDispatchToProps = (dispatch) => {
    return {getRecipient, createNewMessage, clearAllMessageState};
}

export default connect(mapStateToProps, mapDispatchToProps())(ConversationNewMessage);



